.ui-progress{
    -webkit-box-sizing: border-box;
    overflow: hidden;
    width: 100%;
    height: 13px;
    font-size: 0px;
    border: 5px solid #f8f8f8;
    background-color: #e2e2e2
}

.ui-progress span{
    display: block;
    width: 0%;
    background: #65d521;
    height: 100%;
    font-size: 0;
    border-radius: 3px;
}
.ui-grid-trisect li .ui-progress,.ui-grid-halve li .ui-progress{
    position: absolute;
    bottom: 0px;
    z-index: 10;
}